@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.eot");
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../fonts/Roboto-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto-Bold.woff") format("woff"), url("../fonts/Roboto-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.eot");
  src: local("Roboto"), local("Roboto-Regular"), url("../fonts/Roboto-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto-Regular.woff") format("woff"), url("../fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.eot");
  src: local("Roboto Black"), local("Roboto-Black"), url("../fonts/Roboto-Black.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto-Black.woff") format("woff"), url("../fonts/Roboto-Black.ttf") format("truetype");
  font-weight: 900;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Light.eot");
  src: local("Roboto Light"), local("Roboto-Light"), url("../fonts/Roboto-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto-Light.woff") format("woff"), url("../fonts/Roboto-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Thin.eot");
  src: local("Roboto Thin"), local("Roboto-Thin"), url("../fonts/Roboto-Thin.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto-Thin.woff") format("woff"), url("../fonts/Roboto-Thin.ttf") format("truetype");
  font-weight: 100;
  font-style: normal;
}
* {
  font-family: "Roboto", sans-serif;
}

.label {
  margin: 0 auto;
  height: 24px;
  width: 112px;
  background-color: #000;
  font-weight: 400;
  font-size: 11px;
  line-height: 24px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}

.title {
  margin: 37px 0 0;
  font-weight: 700;
  font-size: 38px;
  line-height: 38px;
  text-transform: uppercase;
  text-align: center;
  color: #222;
}

.subtitle {
  margin-top: 14px;
  font-weight: 400;
  font-size: 17px;
  line-height: 17px;
  text-align: center;
  color: #222;
}

.bg-light {
  color: #070716;
  background: #fff;
}

.text-white {
  color: #fff;
}

header {
  position: relative;
  min-height: 52px;
}

.navbar {
  position: fixed;
  z-index: 1000;
  width: 100%;
  min-height: 52px;
  background-color: #212121;
}
.navbar_menu {
  margin: 0;
  padding: 11px 70px;
  display: flex;
  justify-content: space-between;
  list-style: none;
}
.navbar_item:not(:last-child) {
  border-right: 2px dotted #000;
  padding-right: 30px;
}
.navbar_link {
  font-weight: 300;
  font-size: 15px;
  color: #fff;
  line-height: 30px;
}

.subnavbar {
  position: absolute;
  width: 100%;
  top: 52px;
  z-index: 2;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);
}
.subnavbar_logo {
  display: block;
  margin-top: 16px;
  max-width: 100%;
}
.subnavbar_official {
  margin-top: 4px;
  font-weight: 100;
  font-size: 13px;
  color: #fff;
}
.subnavbar_call {
  margin-top: 19px;
  font-weight: 300;
  font-size: 12px;
  text-align: right;
  color: #fff;
}
.subnavbar_phone {
  text-align: right;
}
.subnavbar_phone a {
  font-weight: 400;
  font-size: 22px;
  text-transform: uppercase;
  color: #fff;
}
.subnavbar_btn {
  margin-top: 18px;
  border-radius: 4px;
  width: 172px;
  height: 46px;
  border: none;
  box-shadow: inset 0 -3px 0 0 rgba(0, 0, 0, 0.12);
  background: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
  font-weight: 400;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
}

.promo {
  min-height: 800px;
  background: url(../img/uber_bg.jpg) center center/cover no-repeat;
  padding: 140px 0 185px;
}
.promo_header {
  margin-bottom: 10px;
  font-weight: 900;
  font-size: 60px;
  line-height: 65px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}
.promo_subheader {
  margin-bottom: 16px;
  font-weight: 700;
  font-size: 36px;
  line-height: 45px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}
.promo_desc {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  color: #fff;
}
.promo_btn {
  display: block;
  margin: 110px auto 0;
  border: none;
  border-radius: 4px;
  width: 259px;
  height: 63px;
  box-shadow: inset 0 -4px 0 0 rgba(0, 0, 0, 0.12);
  background: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
  font-weight: 400;
  font-size: 18px;
  text-transform: uppercase;
  color: #fff;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
}

.topping {
  padding: 20px 0 64px;
}
.topping_item {
  display: flex;
  margin-top: 26px;
  height: calc(100% - 26px);
}
.topping_item_round {
  margin: 0 25px 0 33px;
  width: 113px;
  height: 113px;
  background-color: #1eacc7;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  flex: 0 0 auto;
  align-self: center;
}
.topping_item_title {
  margin: 0;
  font-weight: 400;
  font-size: 17px;
  line-height: 21px;
  color: #1eacc7;
}
.topping_item_desc {
  font-weight: 300;
  font-size: 15px;
  line-height: 21px;
  color: #202020;
}
.topping_items div[class^=col]:nth-child(2n) .topping_item_box {
  max-width: 341px;
}
.topping_items div[class^=col]:nth-child(2n+1) .topping_item_box {
  max-width: 371px;
}

.mobile {
  min-height: 864px;
  padding: 46px 0 80px;
  background: url(../img/mobile_bg.png);
}
.mobile_cards {
  margin-top: 30px;
}
.mobile_card {
  position: relative;
  align-items: center;
  margin: 0 auto 19px;
  width: 320px;
  height: 280px;
  padding-top: 90px;
}
.mobile_card_1 {
  background: url(../img/mobile-card/mobile-card-1.png) center center/cover no-repeat;
}
.mobile_card_2 {
  background: url(../img/mobile-card/mobile-card-2.png) center center/cover no-repeat;
}
.mobile_card_3 {
  background: url(../img/mobile-card/mobile-card-3.png) center center/cover no-repeat;
}
.mobile_card_4 {
  background: url(../img/mobile-card/mobile-card-4.png) center center/cover no-repeat;
}
.mobile_card_5 {
  background: url(../img/mobile-card/mobile-card-5.png) center center/cover no-repeat;
}
.mobile_card_6 {
  background: url(../img/mobile-card/mobile-card-6.png) center center/cover no-repeat;
}
.mobile_card_subtitle {
  font-weight: 400;
  font-size: 24px;
  line-height: 31px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}
.mobile_card_plus {
  position: absolute;
  bottom: 34px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 27px;
  height: 27px;
  background: #000;
  border-radius: 100%;
}

.choice {
  padding: 56px 0 138px;
  min-height: 898px;
  background: linear-gradient(to bottom, #070716 502px, transparent 502px), url(../img/mobile_bg.png) 0 503px repeat-x;
}
.choice_card {
  margin-top: 54px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.choice_card_imgbox {
  text-align: center;
}
.choice_card_imgbox img {
  max-width: 100%;
}
.choice_card_desc {
  font-weight: 300;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
  color: #222;
}

.condition {
  min-height: 917px;
  padding: 67px 0 87px;
}
.condition_items {
  margin-top: 33px;
}
.condition_item {
  margin-bottom: 61px;
}
.condition_item_label {
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 22px;
  line-height: 24px;
  color: #1CA8C3;
}
.condition_item_menu {
  margin: 0;
  padding: 0;
}
.condition_item_point {
  margin-bottom: 6px;
  padding-left: 50px;
  font-weight: 300;
  font-size: 17px;
  line-height: 141%;
  color: #222;
  list-style: none;
  background: url(../icons/check_blue.png) 16px top no-repeat;
}
.condition_item_desc {
  margin-top: 25px;
  font-weight: 300;
  font-size: 17px;
  line-height: 24px;
  color: #1CA8C3;
}

.condition_item.danger .condition_item_label {
  color: #FF5656;
}
.condition_item.danger .condition_item_point {
  background: url(../icons/check_red.png) 16px top no-repeat;
}

.cities {
  position: relative;
  padding: 48px 0 45px;
  min-height: 720px;
  background: url(../img/world_bg.png) center center/cover no-repeat;
}
.cities_link {
  position: absolute;
  left: 50%;
  bottom: 45px;
  transform: translateX(-50%);
}
.cities_link a {
  text-transform: uppercase;
  color: #1FBAD6;
  font-size: 15px;
  line-height: 18px;
  font-weight: 400;
}

.footer {
  padding: 13px 0 40px;
  min-height: 155px;
}
.footer .line--blue {
  margin: 0 auto;
  width: 301px;
  height: 7px;
  background-color: #1FBAD6;
}
.footer_wrapper {
  margin-top: 35px;
}
.footer_titles {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.footer_title_item a {
  font-weight: 300;
  font-size: 15px;
  line-height: 20px;
  text-transform: uppercase;
  color: #070716;
}
.footer_subt {
  margin-top: 5px;
  display: flex;
  justify-content: space-between;
}
.footer_subt_item a {
  font-weight: 300;
  font-size: 11px;
  line-height: 15px;
  text-transform: uppercase;
  color: #999999;
}
.footer_lang {
  margin-top: 5px;
  font-weight: 400;
  font-size: 11px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: #1FBAD6;
}
.footer_downapps {
  display: flex;
  justify-content: flex-end;
}
.footer_downapps .down_app {
  margin-left: 6px;
}
.footer_downapps .down_app img {
  width: 95px;
}

.social_items {
  display: flex;
}
.social_item {
  margin-right: 6px;
  width: 33px;
  height: 33px;
  border-radius: 100%;
  background-color: #070716;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social_item a {
  color: #fff;
}

@media (max-width: 1199.98px) {
  .navbar_item:not(:last-child) {
    padding-right: 20px;
  }
  .navbar_link {
    font-size: 13px;
  }
  .promo {
    padding-bottom: 105px;
  }
  .promo_header {
    font-size: 50px;
  }
  .promo_btn {
    margin-top: 90px;
  }
  .mobile_card {
    width: 100%;
  }
  .max-order-lg-first {
    order: -1;
  }
  .social_items, .footer_downapps, .footer_subt, .footer_titles {
    justify-content: center;
  }
  .footer_menu {
    margin-bottom: 18px;
  }
  .footer_title_item {
    margin: 0 23px;
  }
  .footer_subt_item {
    margin: 0 15px;
  }
  .social_items {
    margin-bottom: 18px;
  }
}
@media (max-width: 991.98px) {
  .navbar_item:not(:last-child) {
    padding-right: 8px;
  }
  .navbar_link {
    font-size: 10px;
  }
  .subnavbar_btn, .subnavbar_logo {
    margin-top: 13px;
  }
  .subnavbar {
    height: 74px;
  }
  .subnavbar_logo_img {
    width: 100%;
  }
  .subnavbar_official {
    font-size: 12px;
  }
  .subnavbar_call {
    margin-top: 15px;
    font-size: 10px;
  }
  .subnavbar_phone a {
    font-size: 20px;
  }
  .subnavbar_btn {
    width: 100%;
    font-size: 12px;
  }
  .promo {
    min-height: auto;
    padding: 130px 0 90px;
  }
  .promo_header {
    font-size: 40px;
  }
  .promo_subheader {
    font-size: 30px;
  }
  .promo_desc {
    font-size: 14px;
  }
  .promo_btn {
    margin-top: 60px;
    width: 220px;
    height: 56px;
    font-size: 16px;
  }
  .title {
    font-size: 28px;
    margin-top: 30px;
  }
  .topping_item {
    flex-direction: column;
  }
  .topping_item_title {
    margin-top: 23px;
  }
  .mobile_card {
    height: 255px;
  }
  .mobile_card_subtitle {
    font-size: 16px;
    line-height: 26px;
  }
  .cities {
    min-height: 620px;
  }
}
@media (max-width: 767.98px) {
  header {
    min-height: 42px;
  }
  .navbar {
    min-height: 42px;
  }
  .navbar_menu {
    position: absolute;
    top: 0;
    right: -60%;
    width: 60%;
    height: 100vh;
    background: rgba(33, 33, 33, 0.8);
    flex-direction: column;
    justify-content: center;
    padding: 20px 10px 20px 35px;
    transition: all 0.8s;
  }
  .navbar_menu.active {
    top: 0;
    right: 0;
  }
  .navbar_item:not(:last-child) {
    border: none;
    margin: 3px 0;
  }
  .navbar_link {
    display: block;
    font-size: 17px;
  }
  .subnavbar {
    top: 42px;
  }
  .hamburger {
    display: block;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 26px;
    transition: all 0.8s;
  }
  .hamburger span {
    display: block;
    width: 100%;
    height: 2px;
    margin-bottom: 2px;
    background-color: #fff;
    transition: all 1s;
  }
  .hamburger.active {
    right: 65%;
    right: calc(60% + 10px);
  }
  .hamburger.active span {
    margin-bottom: -2px;
  }
  .hamburger.active span:nth-child(1) {
    transform: translateY(3px) rotate(-45deg);
  }
  .hamburger.active span:nth-child(2) {
    display: none;
  }
  .hamburger.active span:nth-child(3) {
    transform: translateY(3px) rotate(45deg);
  }
  .subnavbar {
    height: 70px;
  }
  .subnavbar_official {
    display: none;
  }
  .subnavbar_logo {
    margin-top: 20px;
  }
  .subnavbar_phone {
    margin-top: 5px;
  }
  .subnavbar_phone a {
    font-size: 15px;
  }
  .subnavbar_btn {
    display: block;
    height: 26px;
    margin: 5px 0 0 auto;
    max-width: 100%;
    width: 180px;
  }
  .promo_header {
    font-size: 35px;
  }
  .topping_items div[class^=col]:nth-child(2n) .topping_item_box, .topping_items div[class^=col]:nth-child(2n+1) .topping_item_box {
    max-width: 371px;
    margin: 0 auto;
  }
  .choice {
    min-height: 700px;
    padding: 56px 0;
  }
  .footer_subt {
    flex-wrap: wrap;
    justify-content: center;
  }
  .footer_subt_item {
    margin: 0 4px;
  }
}
@media (max-width: 575.98px) {
  .subnavbar_phone a {
    font-size: 12px;
  }
  .subnavbar_btn {
    width: 110px;
    height: 22px;
    font-size: 10px;
  }
  .promo_header {
    font-size: 30px;
    line-height: 35px;
  }
  .promo_subheader {
    font-size: 20px;
    line-height: 25px;
  }
  .promo_desc {
    display: none;
  }
  .promo_btn {
    width: 210px;
    height: 50px;
  }
  .title {
    font-size: 16px;
    line-height: 20px;
    margin-top: 16px;
  }
  .subtitle {
    font-size: 14px;
  }
  .choice {
    padding: 26px 0 22px;
    min-height: 393px;
  }
  .choice .title, .choice .subtitle {
    display: none;
  }
  .choice_card_desc {
    color: #fff;
  }
  .condition {
    padding: 33px 0 30px;
  }
  .condition_item {
    margin-bottom: 30px;
  }
  .condition_item_label {
    font-size: 16px;
  }
  .condition_item_point {
    font-size: 14px;
    padding-left: 30px;
    background-position-x: left;
  }
  .condition_item.danger .condition_item_point {
    background-position-x: left;
  }
  .cities {
    padding: 24px 0 29px;
    min-height: 360px;
  }
  .cities_link {
    text-align: center;
  }
  .footer .line--blue {
    max-width: 100%;
  }
  .footer_title_item {
    margin: 0 4px;
  }
  .footer_title_item a {
    font-size: 14px;
  }
  .footer_downapps .down_app img {
    max-width: 100%;
  }
}